import React, { useState } from "react";
import DragAndDropStandAlone from "../DragAndDrop/DragAndDropStandAlone";
import CardComponent from "./Card";
import { useDispatch } from "react-redux";
import { setCAItemObjectDetails, setDroppedObjectData, setPlantObjectData } from "../../store/droppedObjectSlice";

const CardWithDragAndDrop = ({ data, widgetType }) => {
  const dispatch = useDispatch();
  const [isDragging, setIsDragging] = useState(false);
  const handleDragStart = () => {
    // console.log("handleDragStart called");
    setIsDragging(true);
    dispatch(
      setDroppedObjectData({
        cardData: {},
        parentDetails: [],
        versions: [],
        initialDraggedData: [],
      })
    ); // Clear Redux state
    dispatch(
      setPlantObjectData({
        allPlants: [],
        initialAssignedPlants: [],
        uniquePlants: [],
        productChildren: [],
        CAName: false,
        headers: {},
        proposedChanges: [],
        CAData: {},
      })
    );
    dispatch(
      setCAItemObjectDetails({
        CAItemDetails: [],
        CAallPlants: [],
        CAisMFGCA: false,
        CAheaders: {},
      })
    );

  
  
  };

  const handleDragEnd = () => {
    // console.log("handleDragEnd called");
    // console.log("Before setIsDragging(false) - isDragging:", isDragging);
    setIsDragging(false);
    // console.log("After setIsDragging(false) - isDragging:", isDragging);
  };
  return (
    <div className={`card-with-drag-and-drop ${isDragging ? "dragging" : ""}`}>
      <DragAndDropStandAlone widgetType={widgetType} />
      <CardComponent
        data={data}
        onDragStart={handleDragStart}
        onDragEnd={handleDragEnd}
      />
    </div>
  );
};

export default CardWithDragAndDrop;
